import React from 'react';
import './Supplier.scss';
import MiniCard from './MiniCard';

import imgA from '../images/m노상강도_v2.png'


import data from '../data.json'

import imgCurse from '../images/mini/저주.png'
import imgCopper from '../images/mini/동.png'
import imgSilver from '../images/mini/은.png'
import imgGold from '../images/mini/금.png'
import imgEstate from '../images/mini/사유지.png'
import imgDuchy from '../images/mini/공작령.png'
import imgProvince from '../images/mini/속주.png'
import imgBandit from '../images/mini/노상강도.png'
import imgBureaucrat from '../images/mini/관료.png'
import imgLibrary from '../images/mini/도서관.png'
import imgMerchant from '../images/mini/무역상.png'
import imgMine from '../images/mini/광산.png'
import imgMoneylender from '../images/mini/대금업자.png'
import imgRemodel from '../images/mini/개조.png'
import imgSmithy from '../images/mini/대장장이.png'
import imgVillage from '../images/mini/마을.png'
import imgWitch from '../images/mini/마녀.png'

const Supplier = () => {
    //const arNodeTreasure = ["속주", "금", "공작령", "은", "사유지", "동", "저주"];
    //const arNodeAction = ["마을","마을","마을","마을","마을","마을","마을","마을","마을","마을","마을","마을"];
    
    const dtImgToKoreanTable = {imgCurse:	"저주", imgCopper:	"동", imgSilver:	"은", imgGold:	"금", imgEstate:	"사유지", imgDuchy:	"공작령", imgProvince:	"속주", imgBandit:	"노상강도", imgBureaucrat:	"관료", imgLibrary:	"도서관", imgMerchant:	"무역상", imgMine:	"광산", imgMoneylender:	"대금업자", imgRemodel:	"개조", imgSmithy:	"대장장이", imgVillage:	"마을", imgWitch:	"마녀"};
    const dtKoreanToImgTable = {"저주" : imgCurse, "동" : imgCopper, "은" : imgSilver, "금" : imgGold, "사유지" : imgEstate,     "공작령" : imgDuchy, "속주" : imgProvince, "노상강도" : imgBandit, "관료" : imgBureaucrat, "도서관" :     imgLibrary, "무역상" : imgMerchant, "광산" : imgMine, "대금업자" : imgMoneylender, "개조" :     imgRemodel, "대장장이" : imgSmithy, "마을" : imgVillage, "마녀" : imgWitch};

    const arstrBaseSupplyOrder = ["속주", "금", "공작령", "은", "사유지", "동", "저주"];


    const arstrBaseSupplyName = data.stSupplyCard["base"].map((card, i) =>{
        return card.strName;
    });

    return (
        <div>
            <div className="box-container">
                <div className="box1">
                    {arstrBaseSupplyOrder.map((strName, u32I) =>{
                        var u32IndexOfData = arstrBaseSupplyName.indexOf(strName);
                        var stCard = data.stSupplyCard["base"][u32IndexOfData];

                        var strImg = stCard.strName;
                        var stImg = dtKoreanToImgTable[strImg];
                        var u32TheNumber = stCard.u32TheNumber;
                        return (<MiniCard key={u32I} src_img={stImg}>{u32TheNumber}</MiniCard>);
                    })}
                </div>
                <div className="box2">
                    {data.stSupplyCard["action"].map((stCard, u32I) =>{
                        var strImg = stCard.strName;
                        var stImg = dtKoreanToImgTable[strImg];
                        var u32TheNumber = stCard.u32TheNumber;
                        
                        return (<MiniCard key={u32I} src_img={stImg}>{u32TheNumber}</MiniCard>);
                    })}
                </div>
            </div>
        </div>
    );
};

export default Supplier;


